<template>
  <div class="register">
    <van-nav-bar
      left-text="注册"
      title="登录"
      right-text="关闭"
      @click-left="goPage('Register')"
      @click-right="goPage('Menu')"
      left-arrow
    />
    <div class="logo">
      <img class="auto-img" src="../assets/images/logo.jpg" alt />
    </div>
    <!-- @submit="validator" -->
    <van-form @submit="login">
      <van-field v-model="userInfo.phone" type="text" label="手机号" placeholder="手机号" maxlength="11">
        <template v-slot:left-icon>
          <i class="fa fa-phone"></i>
        </template>
      </van-field>
      <van-field
        v-model="userInfo.password"
        left-icon="closed-eye"
        :type="text"
        label="密码"
        placeholder="密码"
        :right-icon="bool ? 'closed-eye' : 'eye-o'"
        @click-right-icon="open"
        maxlength="16"
      >
        <template v-slot:left-icon>
          <i class="fa fa-lock"></i>
        </template>
      </van-field>

      <div class="register-btn">
        <van-button round block type="info" native-type="submit">立即登录</van-button>
        <div class="forgot">
          <span class="fr" @click="forgot">忘记密码？</span>
        </div>
      </div>
    </van-form>
  </div>
</template>

<script>
//导入验证表单模块
import validForm from "../assets/js/ValidForm";
export default {
  data() {
    return {
      userInfo: {
        phone: "",
        password: ""
      },
      text: "password",
      bool: true
    };
  },
  methods: {
    forgot() {
      this.$router.push({ name: "Forgot" });
    },
    //跳转页面
    goPage(name) {
      this.$router.push({ name });
    },
    open() {
      if (!this.bool) {
        this.text = "password";
        this.bool = true;
      } else {
        this.text = "text";
        this.bool = false;
      }
    },
    //登录
    login() {
      //进行验证表单
      if (validForm.validUserForm(this.userInfo, this)) {
        //复制对象
        let data = Object.assign({}, this.userInfo);
        //请求密钥
        data.appkey = this.appkey;
        //开启加载提示
        this.$toast.loading({
          message: "加载中...",
          forbidClick: true,
          //延迟自动关闭加载提示，如果该值为0，则不会自动关闭
          duration: 0
        });
        //发起注册请求
        this.axios({
          method: "POST",
          url: "/login",
          //post传递参数需要写在data
          data
        })
          .then(result => {
            //关闭加载提示
            this.$toast.clear();

            if (result.data.code == 200) {
              //跳转到菜单
              this.$router.push({ name: "Menu" });

              //存储token，用于后面登录验证
              localStorage.setItem("__TK", result.data.token);
            } else {
              this.$toast(result.data.msg);
            }
          })
          .catch(err => {
            //关闭加载提示
            this.$toast.clear();
          });
      }
    }
  }
};
</script>
<style lang="less" scoped>
.forgot {
  margin-top: 20px;
  span {
    font-size: 16px;
    color: brown;
  }
}
.register {
  position: fixed;
  left: 0;
  top: 0;
  bottom: 0;
  right: 0;
  background-color: rgb(246, 246, 246);
  /deep/ .van-nav-bar {
    // height: 50px;
    .van-nav-bar__text {
      color: brown;
    }
  }
  .logo {
    width: 200px;
    height: 200px;
    margin: 0px auto 0px;
  }
  .register-btn {
    margin-top: 30px;
    padding: 0 10px;
  }
  /deep/ .van-cell {
    width: 95%;
    border: 1px solid rgb(215, 180, 148);
    border-radius: 10px;
    margin: 5px auto;
    i {
      width: 20px;
      height: 20px;
      text-align: center;
      line-height: 20px;
      font-size: 18px;
    }
    .van-field__label {
      width: 60px;
    }
  }
  /deep/ .van-button--info {
    background-image: linear-gradient(rgb(215, 180, 148), brown);
    border: none;
  }
}
</style>
